<template>
	
	<view class="audio-warp"  style="width:100%;font-size: 14px;">
		<view class="cover-warp" :class="{ hasbg: !poster }">
			<image class="cover-img" :src="poster" v-if="poster"></image>
			<view  @click='start(audioId)'>
				<view class="play-btn"  v-show='!status' ></view>
				<view class="play-btn pause " v-show='status' ></view>
			</view>
		
		</view>
		<view class="audio-con">
			<view class="audio-time-info">
				<text class="audio-time"> </text>
				<slider class="audio-slider" :block-size="12" block-color="var(--base)" activeColor="var(--base)" @change='changeAudio' :activeColor='activeColor' :min='0' :max='duration.toFixed(0)' :value='currentTime.toFixed(0)' :step='0.1' />
				<text class="audio-time">{{getTime(Math.round(currentTime))}}</text>
			</view>
			<text class="audio-title am-text-eill">{{ name }}</text>
			<!-- <text class="audio-author am-text-eill">{{ author }}</text>-->
		</view>
	</view>
	<!-- 音频播放器组件 -->
 
</template>

<script>
	
	export default {
		name: 'nui-audios',
		data() {
			return {
				context: null,
				currentTime: 0,
				duration: 100,
				status: false
			}
		},
		props: {
			poster: {
				type: String,
				default: ''
			},
			name: {
				type: String,
				default: ''
			},
			author: {
				type: String,
				default: ''
			},
			src: String,
			activeColor: {
				type: String,
				default: 'var(--base)'
			},
			startPic: String,
			endPic: String,
			audioId: [String,Number]
		},
		created() {
			this.context = uni.createInnerAudioContext();
			this.context.src = this.src;
			this.onTimeUpdate();
			this.onCanplay();
			this.onEnded();
			uni.$on('stop',(id)=> {
				if(id && id != this.audioId) {
					this.context.stop();
					this.status = false;
				} else if(!id){
					this.context.stop();
					this.status = false;
				}
			})
		},
		methods: {
			start(id) { //点击播放
				let audioId = id;
				if(this.status) {
					this.context.pause();
					this.status = !this.status;
				}else {
					uni.$emit('stop',id)
					this.context.play()
					this.status = !this.status;
				}
			},
			onCanplay() { //进入可播放状态
				this.context.onCanplay(() => {
					this.context.duration;
					setTimeout(()=>{
						this.duration = this.context.duration;
					},1000)
				})
			},
			onTimeUpdate() { //音频播放进度
				 this.context.onTimeUpdate(() => {
					 if (!Number.isFinite( this.context.duration)) {
						this.context.currentTime = Number.MAX_SAFE_INTEGER;
						this.context.currentTime = 0;
					 } else {
						 this.duration = this.context.duration;
						 this.currentTime = this.context.currentTime;
					 }
				 })
			},
			onEnded() { //播放结束
				this.context.onEnded(()=> {
					this.status = false;
					this.currentTime = 0;
				})
			},
			changeAudio(e) {
				let paused = this.context.paused;
				this.context.pause();
				this.context.seek(e.detail.value)
				if(!paused) {
					this.context.play();
				} 
			},
			getTime(time) {
				let m = parseInt(time / 60);
				let s = time % 60;
				return this.towNum(m) + ':' + this.towNum(s);
			},
			towNum(num) {
				if(num >= 10) {
					return num;
				}else {
					return '0' + num;
				}
			}
		},
	}
</script>

<style lang="scss">
.am-text-eill {
	/*超出省略号*/
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.audio-warp {
	display: flex;
	overflow: hidden;
	height: 50px;
	border-radius: 12px;
 
background: #f5f7f8;
	margin-bottom: 6px;
}

.cover-warp {
	position: relative;
	flex-shrink: 0;
	width:  50px;
	height:  50px;

	&.hasbg {
		background-color: #e6e6e6;
	}

	.cover-img {
		width:  50px;
		height:  50px;
	}

	.play-btn {
		//居中
		position: absolute;		
		left: 50%;
		top: 50%;
		transform: translateX(-50%) translateY(-50%);
		width: 30px;
		height: 30px;		
		border-radius: 50%;
		background-size: 100% 100%;
		background-image: url('data:image/png;base64,');

		&.pause {
			background-image: url('data:image/png;base64,');
		}
	}
}

.audio-con {
	position: relative;
	flex: 1;
	padding:6px 12px;
     font-size: 12px;
     line-height: 13px;

	.audio-title {
		//position: absolute;
		left: 0;
		margin-left: 6px;
	 
	}

	.audio-author {
		//position: absolute;
		//left: 10%;
		bottom: 0;
		color: #888888;
	}

	.audio-time-info {
		display: flex;
		flex-wrap: nowrap;
		//height: 100%;
		align-items: center;
		//要添加该属性，防止滑动报错
		touch-action: none;

		.audio-time {
			color: #9d9d9d;
		}

		.audio-slider {
			flex: 1;
			margin: 3px 6px;
		}
	}
}
</style>
